@media screen and (max-width: 600px) {
    .video-jacker.nft-jacker.drop {

        canvas {
            top: unset;
            bottom: 0px;
            height: 70% !important;
        }

        iframe {
            background-color: #00000075;
            transform: translate(0px, 15px);

        }
        .spore-embed-container {
            z-index: 100;
            background: linear-gradient(124deg, rgba(0, 0, 0, 0.8), rgba(69, 65, 65, 0.8) 50%, rgba(17, 10, 15, 0.8)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
            
            .shuffle-buttons {
                top: 0px !important;
                left: 0px !important;

                .play-btn-wrapper {
                    margin-top: 12px;
                    padding-top: 10px;
                    padding-left: 12px;
                }
                
                .play-and-slider {
                    position: absolute;

                    .slider-scrubber {
                        width: 8px;
                        height: 8px;
                        top: -2px;
                    }

                    .slider-container {
                        width: 100vw;
                        height: 3px;
                        position: absolute;
                        top: 0px;
                    }
                }
            }

            svg {
                display: none;
            }
            .spore-embed-helper {
                position: fixed;
                background-color: #00000096;
                border: 1px solid #ffffff33;
                backdrop-filter: blur(8px);
                padding: 15px;
                width: 100%;
                font-size: 14pt;
                bottom: 0px;
                top: unset;
                left: 0px;
                font-size: 10pt !important;
                z-index: 10000;

                .song-title, .song-artist {
                    padding-left: 40px;
                }
                .song-artist {
                    color: #897e7e;
                }
            }
        }
        
        video {
            top: 0px;
            margin: unset;
            width: 100%;
            height: unset;
            z-index: 10000;
        }
    }
}
